* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #f5f5f5;
  line-height: 1.5;
}

img {
  display: block;
}

.is-hidden {
  display: none;
}

.loader-box {
  justify-content: center;
  display: flex;
}

.loader {
  width: 64px;
  height: 64px;
  background-image: linear-gradient(#000 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#000 16px, rgba(0, 0, 0, 0) 0);
  background-position: 0 0, 0 100%, 100% 0, 100% 100%;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  animation: rotate 1s linear infinite;
  position: relative;
}

@keyframes rotate {
  0% {
    width: 64px;
    height: 64px;
    transform: rotate(0deg);
  }

  50% {
    width: 30px;
    height: 30px;
    transform: rotate(180deg);
  }

  100% {
    width: 64px;
    height: 64px;
    transform: rotate(360deg);
  }
}

.cat-info {
  align-items: center;
  gap: 10px;
  display: flex;
}

.breed-select {
  margin: auto;
  display: flex;
}

.image-container {
  width: 50%;
  height: 50%;
}

.image-container img {
  height: 100%;
  width: 100%;
  border-radius: 30px;
}

.cat-temp-span {
  font-size: 20px;
  font-weight: 900;
}

.cat-body {
  width: 30%;
}

